/* eslint-disable no-unused-vars */
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Navigate, useParams } from "react-router-dom";
import { resetCartAsync } from "../CART/CartSlice";
import { resetOrder } from "../Order/orderSlice";

export default function OrderSuccess() {
  const params = useParams();
  const dispatch = useDispatch();

  useEffect(() => {
    // reset cart
    dispatch(resetCartAsync());
    // reset currentOrder
    dispatch(resetOrder());
  }, [dispatch]);

  if (!params.id) {
    return <Navigate to="/" replace={true} />;
  }

  return (
    <main className="grid min-h-screen place-items-center bg-white px-4 py-16 sm:py-24 lg:py-32">
      <div className="text-center max-w-xl mx-auto">
        <p className="text-base font-semibold text-indigo-600">
          Order Successfully Placed
        </p>
        <h1 className="mt-4 lg:text-lg xl:text-xl font-bold tracking-tight text-gray-900">
          Order Number - {params?.id}
        </h1>
        <p className="mt-6 text-base leading-7 text-gray-600">
          You can check your order in My Account - My Orders
        </p>
        <div className="mt-10 flex items-center justify-center gap-x-6">
          <a
            href="/"
            className="rounded-md bg-indigo-600 px-4 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus:outline-none focus:ring-2 focus:ring-indigo-600 focus:ring-offset-2"
          >
            Go back home
          </a>
        </div>
      </div>
    </main>
  );
}
